body {
    background-color: #18A89C;
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-family: 'Quicksand';
    padding-top: 5%;
    word-wrap: break-word;
  }
  
  .logo {
    width: 250px;
    height: 250px;
  }
  
  .App {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .WalletInfo {
    width: 500px;
  }

  .Blockchain {
    width: 60%;
    margin: 50px auto;
  }
  
  .Block {
    border: 1px solid #fff;
    padding: 5%;
    margin: 2%;
  }
  
  .Transaction {
    padding: 5%;
  }
  
  .ConductTransaction, .TransactionPool {
    margin: 10%;
  }

  .Button {
    opacity: 80%;
    margin: 5px;
  }

  .Button:hover {
    opacity:100%;
  }
  
  a, a:hover {
    color: #FFC300;
    text-decoration: underline;
  }